import React, { useEffect } from "react";
import HeaderTodoApp from "./components/HeaderTodo";
import ComponentView from "./components/ComponentView";
import TodoCreation from "./components/TodoCreation";
import TodoAuth from "./components/TodoAuth";
import TodoProfileSettings from "./components/todoProfileSettings";
import { useSelector } from "react-redux";
export default function App() {
  const active = useSelector((state) => state.todo.authActive);
  let action = false;
  useEffect(() => {
    let token = localStorage.getItem("token");
    token ? (action = false) : (action = true);
  }, []);
  return (
    <div className="w-full table h-full">
      <TodoAuth authActive={action ? false : true} />
      <HeaderTodoApp />
      <TodoProfileSettings />
      <ComponentView />
      <TodoCreation />
    </div>
  );
}
