let todoInt = {
  data: [],
  item: [],
  show: false,
  todoStyle: null,
  style: null,
  user: {},
  authActive: true,
  settingsActive: false,
};

let todoRedStore = (state = todoInt, action) => {
  switch (action.type) {
    case "AUTH":
      let ev = action.payload;
      ev.preventDefault();
      let username = ev.target["username"].value,
        symbol = "@";
      if (username.indexOf(symbol) < 0) {
        localStorage.setItem("user", username);
        ev.target.reset();
        let user = {
          name: username,
          email: "",
          photoURI: "",
        };
        localStorage.setItem("user", JSON.stringify(user));
        return {
          ...state,
          user: JSON.parse(localStorage.getItem("user")),
          authActive: false,
        };
      }
      break;
    case "SUBMIT":
      let current = action.payload;
      current.preventDefault();
      let arr = [];
      let todoData = {
        name: state.user.name,
        title: current.target["title"].value,
        content: current.target["content"].value,
        date: new Date(),
        style: state.todoStyle,
      };

      if (localStorage.getItem("todos")) {
        arr = JSON.parse(localStorage.getItem("todos")) || [];
        arr.push(todoData);
        localStorage.setItem("todos", JSON.stringify(arr));
        window.location.reload();
        return {
          ...state,
          data: arr,
          show: false,
        };
      } else {
        arr.push(todoData);
        localStorage.setItem("todos", JSON.stringify(arr));
        return {
          ...state,
          data: arr,
          show: false,
        };
      }
    case "EDIT":
      return {
        ...state,
        item: [
          {
            single: action.payload,
            index: action.index,
          },
        ],
        show: true,
      };
    case "MODIFY":
      let event = action.payload;
      event.preventDefault();
      let newArr = state.data;
      let newData = {
        title: event.target["title"].value,
        content: event.target["content"].value,
      };
      Object.assign(newArr[state.item[0].index], newData);
      localStorage.setItem("todos", JSON.stringify(newArr));
      window.location.reload();
      return {
        ...state,
        data: newArr,
        show: false,
      };
    case "DELETE":
      let ArrDel = state.data;
      let inDel = ArrDel.indexOf(ArrDel[action.payload]);
      ArrDel.splice(inDel, 1);
      localStorage.setItem("todos", JSON.stringify(ArrDel));
      window.location.reload();
      return { ...state, data: ArrDel };
    case "CHANGE":
      let styles = ["yellow", "red", "green", "purple"];
      let styleIndex = Math.floor(Math.random() * styles.length);
      let styleData = state.data;
      let updateStyle = {
        style: `todo__fig--${styles[styleIndex]}`,
      };
      Object.assign(styleData[action.payload], updateStyle);
      localStorage.setItem("todos", JSON.stringify(styleData));
      window.location.reload();
      return {
        ...state,
        data: styleData,
      };
    case "PHOTO":
      let evn = action.payload;
      if (evn.files[0]) {
        let reader = new FileReader();
        reader.onload = () => {
          let currentUser = state.user,
            photoUpdate = { photoURI: reader.result };
          Object.assign(currentUser, photoUpdate);
          localStorage.setItem("user", JSON.stringify(currentUser));
          return {
            ...state,
            user: currentUser,
          };
        };
        reader.readAsDataURL(evn.files[0]);
      }
      return {
        ...state,
      };
      break;
    case "CHANGES":
      let changesForm = action.payload;
      changesForm.preventDefault();
      let currentFormTarget = changesForm.target;
      if (
        currentFormTarget["username"].value &&
        currentFormTarget["email"].value
      ) {
        let changesUser = state.user,
          newData = {
            name: currentFormTarget["username"].value,
            email: currentFormTarget["email"].value,
            photoURI: JSON.parse(localStorage.getItem("user")).photoURI,
          };
        Object.assign(changesUser, newData);
        console.log(changesUser);
        localStorage.setItem("user", JSON.stringify(changesUser));
        return {
          ...state,
          user: JSON.parse(localStorage.getItem("user")),
          settingsActive: false,
        };
      }
      break;
    case "SETTINGS":
      return {
        ...state,
        settingsActive: action.payload,
      };
      break;
    case "OPEN":
      return {
        ...state,
        show: action.payload,
        todoStyle: action.stTodo,
      };
    case "CLOSE":
      return {
        ...state,
        show: false,
      };
    case "CLOSEMODIFY":
      return {
        ...state,
        item: [],
        show: false,
      };
    default:
      return {
        ...state,
        data: localStorage.getItem("todos")
          ? JSON.parse(localStorage.getItem("todos"))
          : [],
        user: localStorage.getItem("user")
          ? JSON.parse(localStorage.getItem("user"))
          : null,
      };
  }
};

export default todoRedStore;
