import React, { Fragment, useRef } from "react";
import { useSelector, useDispatch } from "react-redux";
import { submitItem } from "../store/todoActoins";
import {
  editCurrentItem,
  CloseCreation,
  CloseModification,
} from "../store/todoActoins";

export default function TodoCreation() {
  const show = useSelector((state) => state.todo.show);
  const todo = useSelector((state) => state.todo.item);
  const dispatch = useDispatch();
  const currentForm = useRef();
  return show ? (
    <div className="fixed inset-0 bg-gray-900 bg-opacity-25 backdrop-filter backdrop-blur-sm z-20 p-4 overflow-x-hidden overflow-y-auto font-medium">
      <div className="block mx-auto rounded-xl bg-gray-800 my-3 w-full max-w-screen-md px-4 py-1">
        <header className="w-full mb-2 border-b border-gray-700">
          <h3 className="text-xl text-white py-3">
            {todo.length > 0 ? (
              <Fragment>
                Edit
                <span className="text-gray-600 truncate inline-block align-middle mx-2 text-sm">
                  "{todo[0].single.title}"
                </span>
              </Fragment>
            ) : (
              "Create Todo Item"
            )}
          </h3>
        </header>
        <form
          className="w-full relative text-white"
          ref={currentForm}
          onSubmit={(event) =>
            todo.length > 0
              ? dispatch(editCurrentItem(event))
              : dispatch(submitItem(event))
          }
        >
          <div className="w-full block my-2 p-3">
            <label htmlFor="title" className="block mb-3">
              Todo Title
            </label>
            <input
              type="text"
              placeholder="Todo title.."
              id="title"
              name="title"
              autoComplete="off"
              spellCheck="true"
              required
              aria-required="true"
              className="w-full py-3 px-5 rounded-xl bg-gray-700 placeholder-gray-500 text-xl"
              dir="auto"
              defaultValue={todo.length > 0 ? todo[0].single.title : null}
            />
          </div>
          <div className="w-full block mt-2 p-3">
            <label htmlFor="content" className="block mb-3">
              Todo Content
            </label>
            <textarea
              type="text"
              placeholder="Todo Content.."
              id="content"
              name="content"
              autoComplete="off"
              spellCheck="true"
              required
              aria-required="true"
              className="w-full py-3 px-5 rounded-xl bg-gray-700 placeholder-gray-500 text-xl resize-y"
              dir="auto"
              defaultValue={todo.length > 0 ? todo[0].single.content : null}
              style={{ minHeight: 125 }}
            ></textarea>
          </div>

          <div className="w-full block border-t border-gray-700 p-3 text-right">
            <button
              className="inline-block mx-1 py-3 px-5 truncate text-red-500 bg-transparent"
              onClick={() =>
                todo.length > 0
                  ? dispatch(CloseModification(false, {}, currentForm))
                  : dispatch(CloseCreation(false, currentForm))
              }
            >
              Cancel
            </button>
            <button className="inline-block mx-1 py-3 px-5 truncate text-blue-200 bg-blue-500 rounded-2xl">
              Submit Item
            </button>
          </div>
        </form>
      </div>
    </div>
  ) : null;
}
