import React, { Fragment, useRef } from "react";
import { useDispatch } from "react-redux";
import { editItem, deleteItem, changeStyle } from "../store/todoActoins";

export default function SingleTodoItem(props) {
  const dispatch = useDispatch();
  const dropdown = useRef();
  const getTimeFromCurrentData = (date) => {
    let currentDate = new Date();
    let itemDate = new Date(date);

    let itemTime = itemDate.getTime();
    let currentTime = currentDate.getTime();

    let finalTime = currentTime - itemTime,
      second = 1000,
      minute = second * 60,
      hour = minute * 60,
      day = hour * 24;
    let fDay = Math.floor(finalTime / day),
      fHour = Math.floor((finalTime % day) / hour),
      fMinute = Math.floor((finalTime % hour) / minute),
      fSecond = Math.floor((finalTime % minute) / second),
      fTime = "";
    if (fDay > 0) {
      fTime = fDay + "day";
    } else if (fDay < 0) {
      fTime = fHour + "hr";
    } else if (fHour > 0 && fHour < 24) {
      fTime = fHour + "hr";
    } else if (fMinute > 0 && fMinute < 60) {
      fTime = fMinute + "min";
    } else if (fSecond > 0 && fSecond < 60) {
      fTime = fSecond + "sec";
    }

    return fTime;
  };

  const openDropDown = (index) => {
    const dropDownItem = document.querySelectorAll(
      `.${dropdown.current.classList[0]}`
    )[index];
    dropDownItem.classList.toggle("todo__fig--active");
  };

  const closeCurrentDrop = (index) => {
    document
      .querySelectorAll(`.${dropdown.current.classList[0]}`)
      [index].classList.remove("todo__fig--active");
  };

  const data = props.items.map((item, index) => (
    <div
      className="w-full"
      key={item.id ? item.id.toString() : index.toString()}
    >
      <figure className={`todo__fig ${item.style}`}>
        <blockquote className="w-full">
          <div className="todo__fig--header relative">
            <div className="truncate">
              <h5 className="todo__fig--name">- {item.name}</h5>
              <span className="block leading-4 text-xs px-3">
                {getTimeFromCurrentData(item.date)}
              </span>
            </div>

            <button
              className="todo__fig--btn mx-1 py-1"
              onClick={() => openDropDown(index)}
            >
              <svg
                xmlns="http://www.w3.org/2000/svg"
                className="h-5 w-5"
                fill="none"
                viewBox="0 0 24 24"
                stroke="currentColor"
              >
                <path
                  strokeLinecap="round"
                  strokeLinejoin="round"
                  strokeWidth="2"
                  d="M12 5v.01M12 12v.01M12 19v.01M12 6a1 1 0 110-2 1 1 0 010 2zm0 7a1 1 0 110-2 1 1 0 010 2zm0 7a1 1 0 110-2 1 1 0 010 2z"
                />
              </svg>
            </button>
            <div
              className="todo__fig--drop"
              ref={dropdown}
              onClick={() => closeCurrentDrop(index)}
            >
              <ul>
                <li className="w-full my-1 p-1">
                  <button
                    onClick={() => dispatch(editItem(item, index))}
                    className="text-left"
                  >
                    Edit
                  </button>
                </li>
                <li className="w-full my-1 p-1">
                  <button
                    className="text-left"
                    onClick={() => dispatch(changeStyle(index))}
                  >
                    Change Style
                  </button>
                </li>
                <li className="w-full my-1 p-1">
                  <button
                    className="text-left"
                    onClick={() => dispatch(deleteItem(index))}
                  >
                    Delete
                  </button>
                </li>
              </ul>
            </div>
          </div>
          <p className="p-2 py-3 break-words">{item.content}</p>
        </blockquote>
        <figcaption className="w-full pt-2 border-t">
          <h5 className="text-sm text-gray-500 py-2 px-1">{item.title}</h5>
        </figcaption>
      </figure>
    </div>
  ));

  return <Fragment>{data}</Fragment>;
}
