import React from "react";
import { useDispatch, useSelector } from "react-redux";
import { submitAuth } from "../store/todoActoins";
export default function TodoAuth() {
  const username = useSelector((state) => state.todo.user.name);
  const dispatch = useDispatch();
  const handleChange = (current) => {
    let currentValue = current.value,
      symbol = "@";
    if (currentValue.indexOf(symbol) >= 0) {
      current.classList.add("bg-red-400", "text-red-700");
    } else {
      current.classList.remove("bg-red-400", "text-red-700");
    }
  };
  return !username ? (
    <div className="fixed inset-0 bg-gray-900 bg-opacity-25 backdrop-filter backdrop-blur-sm z-20 p-4 overflow-x-hidden overflow-y-auto font-medium">
      <div className="block mx-auto rounded-xl bg-gray-800 my-3 w-full max-w-screen-md px-4 py-1">
        <header className="w-full mb-2 border-b border-gray-700">
          <h3 className="text-xl text-white py-3">Welcome, Dear</h3>
        </header>
        <form
          className="w-full relative text-white"
          onSubmit={(event) => dispatch(submitAuth(event))}
        >
          <div className="w-full block  p-3">
            <label htmlFor="username" className="block mb-3">
              @username
            </label>
            <input
              type="text"
              placeholder="@username.."
              id="username"
              name="username"
              autoComplete="off"
              spellCheck="true"
              required
              aria-required="true"
              className="w-full py-3 px-5 rounded-xl bg-gray-700 placeholder-gray-500 text-xl"
              dir="auto"
              onChange={(event) => handleChange(event.target)}
            />
            <p className="text-sm text-gray-600 p-3">
              Type a correct name without using that symbol "@"
            </p>
          </div>
          <div className="w-full block border-t border-gray-700 p-3 text-right">
            <button className="inline-block mx-1 py-3 px-5 truncate text-blue-200 bg-blue-500 rounded-2xl">
              Submit
            </button>
          </div>
        </form>
      </div>
    </div>
  ) : null;
}
