import React from "react";
import "./componentView.css";
import SingleTodoItem from "./singleTodoItem";
import { useSelector } from "react-redux";
export default function ComponentView() {
  const todos = useSelector((state) => state.todo.data);
  return todos.length > 0 ? (
    <section className="todo__fig--container">
      <SingleTodoItem items={todos} />
    </section>
  ) : (
    <div className="table mx-auto py-10 px-4 text-gray-700 text-center">
      <p className="text-xl max-w-xl">
        There are no items to display, click on the create button " + " at the
        top and choose the appropriate color for you and add a new item
      </p>
    </div>
  );
}
