const submitItem = (value) => {
  return {
    type: "SUBMIT",
    payload: value,
  };
};

const openCreation = (value, todoType) => {
  return {
    type: "OPEN",
    payload: value,
    stTodo: todoType,
  };
};

const CloseCreation = (value, currentForm) => {
  return {
    type: "CLOSE",
    payload: value,
    form: currentForm,
  };
};
const CloseModification = (value, item, mForm) => {
  return {
    type: "CLOSEMODIFY",
    payload: value,
    item: item,
    form: mForm,
  };
};

const editItem = (value, index) => {
  return {
    type: "EDIT",
    payload: value,
    index: index,
  };
};

const editCurrentItem = (value) => {
  return {
    type: "MODIFY",
    payload: value,
  };
};

const deleteItem = (value) => {
  return {
    type: "DELETE",
    payload: value,
  };
};

const changeStyle = (value) => {
  return {
    type: "CHANGE",
    payload: value,
  };
};

const submitAuth = (event) => {
  return {
    type: "AUTH",
    payload: event,
  };
};
const changePhoto = (event) => {
  return { type: "PHOTO", payload: event };
};
const submitChanges = (event) => {
  return {
    type: "CHANGES",
    payload: event,
  };
};
const openSettings = (ac) => {
  return {
    type: "SETTINGS",
    payload: ac,
  };
};
export {
  submitItem,
  openCreation,
  editItem,
  editCurrentItem,
  CloseCreation,
  CloseModification,
  deleteItem,
  changeStyle,
  submitAuth,
  changePhoto,
  submitChanges,
  openSettings,
};
